@import "./common/global";

.header {
	position: fixed;
	left: 0;
	right: 0;
	height: 130px;
	background-color: #00434e;
	padding: 25px;
	display: flex;
	justify-content: space-evenly;
	color: white;
	text-transform: uppercase;
	z-index: 100;
	transition: all 1s ease;
	a {
		color: white;
	}
	&__logo {
		margin-right: 20px;
		img {
			max-width: 300px;
		}
	}
	&__inner {
		display: flex;
		flex-direction: column;
		justify-content: center;
		&-top {
			display: flex;
			align-items: center;
			margin-bottom: 15px;
		}
	}
	&__list {
		display: flex;
	}
	&__link {
		padding: 5px;
		margin-right: 40px;
		cursor: pointer;
		border-bottom: 2px solid transparent;
		transition: all 0.2s ease-in-out;
		&:hover {
			border-bottom: 2px solid white;
		}
	}
	&__phone {
		a {
			margin-right: 60px;
			position: relative;
			margin-left: 30px;
			&::before {
				content: "";
				position: absolute;
				background: url("../img/icons/call.webp");
				background-size: contain;
				background-repeat: no-repeat;
				top: -2px;
				left: -30px;
				width: 25px;
				height: 25px;
				cursor: default;
			}
		}
	}
	&__call {
		padding: 5px;
		display: inline-block;
		border: 1px solid white;
		border-radius: 5px;
		cursor: pointer;
	}
	&__description {
		font-size: 23px;
	}
}
.header-fixed {
	padding: 0;
	height: 100px;
}
.section-top {
	display: flex;
	flex-direction: column;
	justify-content: center;
	background: url("../img/1.webp") no-repeat;
	background-size: cover;
	background-position: center;
	background-attachment: fixed;
	position: relative;
	z-index: 10;
	padding: 120px;
	text-align: center;
	color: white;
	&::before {
		content: "";
		position: absolute;
		display: block;
		top: 0;
		bottom: 0;
		right: 0;
		left: 0;
		background-color: rgba(black, 0.3);
		z-index: -1;
	}
	&__title {
		max-width: 1020px;
		margin: 0 auto;
		font-size: 40px;
		line-height: 57px;
		margin-bottom: 50px;
	}
	&__button {
		margin-bottom: 100px;
	}
	&__items {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}
	&__item {
		text-transform: uppercase;
		&-text {
			margin-bottom: 20px;
			font-size: 35px;
			letter-spacing: 5px;
		}
		&-img {
			img {
				width: 100px;
			}
		}
	}
}

.section-about {
	padding-top: 150px;
	background-color: #ececec;
	&__inner {
		padding: 80px 60px;
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	&__items {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-evenly;
		margin-bottom: 100px;
	}
	&__item {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		min-width: 50%;
		margin-bottom: 50px;
		&-img {
			margin-bottom: 20px;
			img {
				width: 150px;
			}
		}
		&-text {
			text-align: center;
			max-width: 320px;
			line-height: 20px;
		}
	}
}

.section-work {
	padding: 100px;
	padding-top: 150px;
	&__inner {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	&__title {
		text-align: center;
		font-size: 48px;
		color: #00434e;
		margin-bottom: 40px;
	}
	&__items {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin-bottom: 60px;
	}
	&__item {
		display: flex;
		max-width: 32%;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		margin-bottom: 30px;
		&-text {
			text-align: center;
			margin-bottom: 30px;
			width: 210px;
			height: 30px;
		}
		&-img {
			overflow: hidden;
			height: 300px;
			img {
				max-width: 100%;
				height: 100%;
				transition: all 0.5s ease;
				&:hover {
					transform: scale(1.2);
				}
			}
		}
	}
}

.section-price {
	background: url("../img/8.webp");
	background-size: cover;
	background-position: center;
	background-attachment: fixed;
	position: relative;
	color: white;
	z-index: 10;
	padding: 50px;
	padding-top: 150px;
	&::after {
		content: "";
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		background-color: rgba(black, 0.4);
		z-index: -1;
	}
	&__title {
		text-align: center;
		font-size: 46px;
		text-transform: uppercase;
		margin-bottom: 30px;
	}
	&__inner {
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding: 0 50px;
	}
	&__box {
		margin-bottom: 40px;
		ul {
			font-size: 24px;
			li {
				display: flex;
				justify-content: space-between;
				margin-bottom: 10px;
				.offset {
					margin-left: 30px;
				}
				div:nth-child(2) {
					flex: 1 0;
					border-bottom: 5px dotted white;
				}
			}
		}
	}
}

.section-gallery {
	padding: 100px;
	padding-top: 150px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	&__title {
		text-align: center;
		font-size: 48px;
		color: #00434e;
		margin-bottom: 40px;
		text-transform: uppercase;
	}
	&__items {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}
	&__item {
		max-width: 32%;
		overflow: hidden;
		position: relative;
		margin-bottom: 40px;

		img {
			width: 100%;
			transition: all 0.5s ease;
		}
		&::after {
			content: "";
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			background-color: rgba(black, 0.2);
		}
		&:hover img {
			transform: scale(1.2);
		}
	}
}

.section-feedback {
	padding: 100px;
	display: flex;
	flex-direction: column;
	background-color: #8d8c8d;
	color: white;
	&__title {
		text-align: center;
		font-size: 48px;
		margin-bottom: 40px;
		text-transform: uppercase;
		margin-bottom: 80px;
	}
	&__items {
	}
	&__item {
		display: flex;
		justify-content: space-evenly;
		align-items: center;
		margin-bottom: 100px;
		&-img {
			width: 200px;
			height: 200px;
			overflow: hidden;
			border-radius: 50%;
			img {
				width: 100%;
				height: 100%;
			}
		}
		&-text {
			display: block;
			max-width: 72%;
			font-size: 24px;
		}
	}
}

.quest {
	padding: 100px 130px;
	&__title {
		text-align: center;
		font-size: 36px;
		margin-bottom: 40px;
		margin-bottom: 80px;
	}
	&__box {
		display: flex;
		justify-content: space-around;
		align-items: center;
		&-img {
			width: 300px;
			img {
				width: 100%;
				height: 100%;
			}
		}
		&-btn {
			background-color: transparent;
			color: #00434e;
			border: 3px solid #00434e;
			padding: 25px 60px;
		}
	}
}
.section-contact {
	padding: 200px;
	background-color: #8d8c8d;
	position: relative;
	&__box {
		display: flex;
		justify-content: space-around;
		align-items: center;
		&-map {
			width: 40%;
			height: 300px;
		}
	}
	&__footer {
		position: absolute;
		text-align: center;
		color: white;
		font-size: 14px;
		bottom: 10px;
		left: 0;
		right: 0;
		p {
			line-height: 20px;
		}
	}
	.contact {
		background-color: white;
		padding: 20px;
		&__box {
			border: 3px solid #00434e;
			padding: 40px;
			text-align: center;
		}
		&__street,
		&__floor,
		&__phone,
		&__time,
		&__email {
			font-size: 24px;
			margin-bottom: 10px;
		}
		&__messenger {
			display: flex;
			align-items: center;
			justify-content: center;
			font-size: 24px;
			&-item {
				margin: 0 5px;
				a {
					.fa {
						display: block;
						color: white;
						padding: 6px;
						font-size: 20px;
					}
					.fa-vk {
						background-color: #507298;
					}
					.fa-instagram {
						background-color: #e22d45;
					}
					.fa-whatsapp {
						background-color: #01e675;
					}
					.fa-telegram {
						background-color: #0088cc;
					}
				}
			}
		}
	}
}

// modal
.modal-call {
	visibility: hidden;
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background: rgba(black, 0.5);
	z-index: 1000;
	display: flex;
	justify-content: center;
	align-items: center;
	&__form {
		min-width: 600px;
		position: relative;
		&-box {
			background-color: white;
			color: #00434e;
			padding: 40px;
			border-radius: 5px;
		}
		&-title {
			margin-bottom: 20px;
			font-size: 24px;
			text-align: center;
		}
		&-input {
			display: flex;
			flex-direction: column;
			margin-bottom: 20px;
			label {
				margin-bottom: 5px;
			}
			input {
				border: 1px solid black;
				border-radius: 5px;
				padding: 5px;
			}
		}
		&-close {
			position: absolute;
			top: 10px;
			right: 10px;
			font-size: 24px;
			cursor: pointer;
		}
		&-btn {
			button {
				background-color: #00434e;
				color: white;
				padding: 10px;
			}
		}
	}
}

.scrolltop {
	visibility: hidden;
	display: flex;
	justify-content: center;
	align-items: center;
	color: white;
	font-size: 24px;
	position: fixed;
	bottom: 30px;
	right: 30px;
	width: 60px;
	height: 60px;
	border-radius: 50%;
	background-color: #00434e;
	cursor: pointer;
	z-index: 1000;
}

.open {
	visibility: visible;
}
@import "./common/media";
