@import "../../../node_modules/normalize.css/normalize";
@import "./fonts";
@import "../mixins/fonts";
@import "../variables/variables";
@import "../nullstyle/nullstyle";
@import "footer";
@import "header";
@import 'https://cdnjs.cloudflare.com/ajax/libs/animate.css/4.1.1/animate.min.css';
body {
	font-family: $font-family;
}
.container {
	max-width: $container;
	margin: 0 auto;
	padding: 0 15px;
}
.imgf {
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	position: relative;
	> img {
		width: 0;
		height: 0;
		position: absolute;
		top: 0;
		left: 0;
		opacity: 0;
		visibility: hidden;
	}
}

.btn {
	border: 3px solid transparent;
	color: white;
	display: inline-block;
	padding: 20px;
	border-radius: 20px;
	margin: 0 auto;
	text-transform: uppercase;
	font-size: 40px;
	background-color: #00434e;
	cursor: pointer;
	transition: all .2s ease-outA;
	&:hover {
	color: #00434e;
	background-color: white;
	border: 3px solid #00434e;


	}
}