/* Large desktops and laptops */

@media (max-width: 1440px) {
	.header__menu ul li {
		margin-right: 0px;
		font-size: 16px;
		cursor: pointer;
	}
	.header__description {
		font-size: 18px;
		text-align: center;
	}
	.header__phone {
		margin-left: 30px;
		margin-right: 30px;
	}
	.header__phone a {
		margin-right: 0px;
	}
	.section-contact .contact {
		margin-right: 30px;
	}
}
/* Portrait tablets and medium desktops */
@media (min-width: 992px) and (max-width: 1199px) {
	.header__logo img {
		max-width: 230px;
	}
	.header__description {
		font-size: 18px;
		text-align: center;
	}
	.header__inner-top {
		flex-wrap: wrap;
		justify-content: center;
	}
	.header__menu ul li {
		margin-right: 30px;
	}
	.header__menu ul {
		margin-bottom: 10px;
	}
	.section-top {
		height: auto;
	}
	.section-work__item {
		max-width: 100%;
		width: 48%;
	}
	.section-feedback__item-text {
		text-align: center;
	}

	.section-feedback__item {
		flex-direction: column;
		justify-content: center;
	}
	.section-feedback__item-img {
		margin-bottom: 40px;
	}
	.section-top__item-text {
		font-size: 24px;
	}
	.quest__box-btn {
		font-size: 25px;
		width: 375px;
		margin: 0px 36px;
	}
	.section-contact .contact {
		margin-bottom: 40px;
	}
	.section-contact__box {
		flex-wrap: wrap;
	}
}

